import Toast from '../../wxcomponents/vant/dist/toast/toast';
import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
export default {
	
	data() {
		return {			
			list1:[],				
			page1:1,
			loading1:false,
			showTitle:false,
			allow0:false
			
		}
	},
	onLoad() {
		this.getFan()
	},
	// 上拉加载更多
	onReachBottom(){
		if(this.allow0){
			this.allow0 = false
			this.loading1 = true
			this.page1++
			this.getFan()
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		this.page1 = 1
		this.getFan()
		setTimeout(function () {
		        uni.stopPullDownRefresh();
				uni.showToast({
				    title: '刷新成功',
				    duration: 1000,
					icon:'none'
				});
		  }, 1000)
	},
	methods: {
		
		getFan(){
			this.$api.new_users({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				page:this.page1
			}).then(res=>{
				// console.log(res)
				if (res.data.code === '200') {
					
					
					// 第一页的时候
					if(this.page1 === 1){
						this.list1 = res.data.data
						// 大于20条允许 继续加载,少于20条不允许加载并且显示没有更多
						if(res.data.data.length>=20){
							// 控制是否允许加载的变量
							this.allow0 = true
						}else{
							// 控制没有更多的变量
							this.showTitle = true
							this.allow0 = false
						}														
					}else{
						// 第二页的时候
						this.list1 = this.list1.concat(res.data.data)
						if(res.data.data.length>=20){
							this.allow0 = true
						}else{
							this.showTitle = true
							this.allow0 = false
						}	
					}
					// 加载进度条,加载完都得去掉
					this.loading1 = false
					
					
					  // if (this.page1 === 1) {
						 //  this.list1 = res.data.data
						 //  this.loading1 = false
					  // } else {
						 //  if(res.data.data.length){
							//   setTimeout(()=>{
							// 	  this.list1 = this.list1.concat(res.data.data)
							// 	  this.loading1 = false
							// 	  uni.showToast({
							// 		title:'加载成功',
							// 		icon:"none"
							// 	  })
							//   },1500)
						 //  }else{
							//   this.loading1 = false
							//   uni.showToast({
							// 	title:'没有更多的数据了',
							// 	icon:"none"	,								  	
							//   })
						 //  }
					  // }
					
				}else if (res.data.code === '1010') {
				  uni.clearStorageSync()
				  uni.showToast({
				      title: '登录过期，请重新登录',
				      duration: 2000,
				  	icon:'none'
				  });
				  
				  uni.reLaunch({
				  	url: '../../pages/login/login'
				  })
				}
				// console.log(this.list1)
			})
		},
		
		MyInfo(id){
			uni.navigateTo({
				url:'../../pagesMy/MyInfo/MyInfo?id='+id
			})
		},
		// 点击关注 取消等
		vote (item) {
		  if (item.status === 1 || item.status === 2) { // 如果是是互相关注，或者是已关注，点击即是取消关注，显示关注
			Dialog.confirm({
			  title: '提示',
			  message: '确定取消关注吗？'
			}).then(() => {
				this.follow(item)
			}).catch(() => {
			  // on cancel
			});
		    
		  } else if (item.status === 3) { // 如果是被关注，点击关注显示互相关注
		    this.follow(item)
		  } else if (item.status === 4) { // 如果是都未关注，显示已关注
		    this.follow(item)
		  }
		},
		follow (item) {
		  this.$api.NewsFollow({
		    token: uni.getStorageSync("token"),
		    user_id: uni.getStorageSync("userId"),
		    follow_id: item.user_id
		  }).then(res => {
		    item.status = res.data.data
		    Toast(res.data.msg)
			
		  })
		}
	}
}
